import { Component, useEffect, useState } from 'react'
import { View, Button, Text, Input, Image } from '@tarojs/components'
import { fetchListenList } from '../../utils/api'
import Taro from '@tarojs/taro'
import './style.scss'

import {
  QfSearch,
  QfBookCard
} from '@/components'

export default props => {

  const [list, setList] = useState([])

  useEffect(()=>{
    fetchListenList({}).then(res=>{
      console.log('听书列表', res)
      setList(res.list)
    })
  }, [])

  const skipToDetail = ele => {
    Taro.navigateTo({
      url: '/pages/listen_detail/listen_detail?id='+ele._id
    })
  }

  const renderList = ()=> {
    return list.map(ele=>(
      <View className='row' key={ele._id} onClick={()=>skipToDetail(ele)}>
        <View className='left'>
          <Image src={'http://localhost:8888'+ele.img} />
        </View>
        <View className='right'>
          <View className='book'>{ele.book_name}</View>
          <View className='user'>讲书人：{ele.tell}</View>
          <View className='desc'>{ele.desc}</View>
        </View>
      </View>
    ))
  }



  return (
    <View className='qf-listen'>

      <QfSearch disabled />

      <QfBookCard />

      { renderList() }
    </View>
  )
}
